.content_box {
  width: 458px;
  height: 260px;
  background: #fff;
  margin: 0px 36px 16px 0;
  border-radius: 22px;
  box-sizing: border-box;
  padding: 12px 22px;
  display: flex;
  flex-direction: column;
}

.content_box > .content_box_header:first-child {
  height: 28px;
  overflow: hidden;
  display: flex;
  font-size: 12px;
}

.content_box > .content_box_header:first-child > p {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
  align-items: center;
  border-left: 3px solid #0C44A1;
  color: #0C44A1;
  height: 22px;
  padding-left: 14px;
  margin: 0;
}

.content_box > .content_box_header:first-child > div {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 24px;
}

.content_box > .content_box_header:first-child > div > span {
  padding: 0 7px;
}

.content_box > .content_box_header:last-child {
  flex: 1;
  overflow: hidden;
}

.content_box > .content_box_cont {
  flex: 1;
}

.content_box > .content_box_cont h2 {
  width: 100%;
  height: 250px;
  text-align: center;
  line-height: 250px;
  font-size: 40px;
  color: orange;
}

.content_box > .content_box_cont h2 > span {
  font-size: 14px;
  color: #ccc;
}

.content_box > .content_box_cont p {
  text-align: center;
}

.content_box > .content_box_cont #myEcharts {
  width: 100%;
  height: 100%;
}
